header{
  text-align: center;
  width: 100%;
}
.logo{
  height: 300px;
  max-width: 100%; 
  max-height: 100%;
}
.logo-description{
  font-size: smaller;
  max-width: 90%;
  margin: auto;
}
.inputform-groupbox{
  border:1px solid #000;/*線*/
  color:#000;/*文字色*/
  border-radius: 5px;/*角の丸み*/
  width: 70%;
  margin: auto;
}
.inputform-groupbox #textarea{
  border: none;
  width: 100%;
  height: 250px;
  font-family: Arial, Helvetica, sans-serif;
}
.inputform-groupbox #title{
  font-size: 80%;
  color: #808080;
}
.inputform-button{
  text-align: center;
  margin-top: 5px;
  width: 100%;
}
.inputform-button #button{
  font-size: 150%;
  color: #000;
  border: 1px solid #000;
  line-height: 1.75;
  border-radius: 4px;
  letter-spacing: 0.05em;
  background-color: #fff;
}
